import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

const AtentionClient = (props) => {
  const { infos } = props;

  return(
    <div>
      <h2>Chamado enviado!</h2>
      <p>Chegaremos assim que possvel.</p>
      <p>Enquanto isso voc pode ver nosso cardpio online no boto abaixo:</p>
      <div>
        <Link to={`/client/cardapio/?${infos._id}`}>Ver cadpio online</Link>
      </div>
    </div>
  );
}

AtentionClient.prototypes = {
  infos: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  infos: state.client.infos
});

export default connect(mapStateToProps)(AtentionClient);